	module.exports = {"data":{"post":{"html":"<p>We haven't made any progress on the house pet front, but the ongoing negotiations are encouraging. We do have some further details on why Christopher is insisting on a dalmatian.</p>\n<p>>Mama: What about a beagle. They're cute and have such floppy ears.</p>\n<p>>Christopher: But beagles are brown. I want a dalmatian that's black and white. Black and white are my favorite colors.</p>\n<p>>Mama: Why is that?</p>\n<p>>Christopher: Black is in the German flag and white is in the American flag. So a dalmatian is German and American, just like me.</p>\n<p>Can't argue with that logic.</p>","fields":{"title":"Black and White","date":"08 Apr 2008","url":"https://papascott.de/2008-04-08-black-and-white/"}}},"pathContext":{"slug":"/2008-04-08-black-and-white/","prev":{"fields":{"title":"Addition to the Family","date":"30 Mar 2008","slug":"/2008-03-30-addition-to-the-family/"}},"next":{"fields":{"title":"Open All Night","date":"08 Apr 2008","slug":"/2008-04-08-open-all-night/"}}}}

